

.gameward_overlay {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: auto;
  background: transparent;
  border: none;
  border-radius: 0;
  padding: 20px;
  font-family: 'Roboto', sans-serif;
  color: #ffffff;
  z-index: 1000;
  overflow: visible;
}

.player_stats_container {
  background: #2a274e;
  border-radius: 15px;
  padding: 0;
  margin: 0 0 25px 0;
  border: 2px solid #fbb804;
  display: flex;
  flex-direction: column; 
  align-items: stretch;
  box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  width: 100%; 
  min-width: 500px;
  max-width: 800px;
  overflow: hidden;
}

.player_stats_container:last-child {
  margin-bottom: 0;
}

.player_header {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0 30px;
  background: #2a274e;
  border-radius: 12px 0 0 12px;
  min-width: fit-content;
  flex-shrink: 0;
}

.player_avatar {
  width: 55px;
  height: 55px;
  border-radius: 50%;
  margin-right: 15px;
  border: 3px solid #fbb804;
  object-fit: cover;
}

.player_name {
  font-size: 2.0em;
  font-weight: 900;
  margin: 0;
  color: #fbb804;
  text-shadow: none;
  letter-spacing: 2px;
  text-transform: uppercase;
  white-space: nowrap;
  text-align: center;
}

.player_top_section {
  display: flex;
  flex-direction: row;
  align-items: stretch;
  height: 70px;
}

.stats_display {
  display: flex;
  flex-direction: row;
  padding: 0;
  margin: 0;
  border-radius: 0 12px 12px 0;
  overflow: hidden;
  flex: 1;
}

.stat_column {
  flex: 1;
  text-align: center;
  padding: 12px 20px;
  background: #2a274e;
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-width: 90px;
  position: relative;
}

.stat_column::after {
  content: '';
  position: absolute;
  right: 0;
  top: 20%;
  bottom: 20%;
  width: 2px;
  background-color: #fbb804;
}

.stat_column:last-child::after {
  display: none;
}
.resume_label {
  font-size: 1.2em;
  font-weight: 800;
  color: #fbb804;
  text-transform: uppercase;
  letter-spacing: 1.5px;
  padding: 12px 16px;
  text-align: center;
  border-top: 2px solid #fbb804;
  margin: 8px 10px 6px;
  background-color: rgba(251, 184, 4, 0.12);
  border-radius: 8px;
}

.stat_label {
  font-size: 0.85em;
  font-weight: 700;
  margin-bottom: 3px;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.stat_value {
  font-size: 2em;
  font-weight: bold;
  color: #ffffff;
  margin: 0;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
}

.error_container {
  text-align: center;
  padding: 40px 30px;
  background: #504588;
  border-radius: 0 0 10px 10px;
  font-size: 1.2em;
  font-weight: 700;
  color: #ffffff;
}

.error_text {
  font-size: 18px;
  color: #ffffff;
  text-align: center;
}


@media (max-width: 768px) {
    .gameward_overlay {
        padding: 10px;
    }
    
    .player_stats_container {
        margin-bottom: 15px;
    }
    
    .player_avatar {
        width: 50px;
        height: 50px;
    }
    
    .player_name {
        font-size: 1.2em;
    }
    
    .stat_value {
        font-size: 1.6em;
    }
}